@media (min-width: 700px) {
  /* #photos {
  -moz-column-count:    3;
  -webkit-column-count: 3;
  column-count:         3;
  } */
  .nav-item {
    padding: 0.9em;
  }


  .price-container {
    -webkit-column-count: 3;
    column-count:         3;
    -webkit-column-gap: 1em;
            column-gap: 1em;
    -webkit-column-width: 25%;
            column-width: 25%;
    height: auto;
    margin-left: 25%;
    margin-right: auto;
    font-size: 1em;
  }
}
@media (min-width: 425px) {
  #photos {
  -webkit-column-count: 2;
  column-count:         2;
  }

  .price-container {
    -webkit-column-count: 2;
    column-count:         2;
    height: auto;
    font-size: 0.5em;
  }

}

@media screen and (min-width: 425px) and (max-width: 699px) {
  .business-logo-container {
    width: 30vw;
  }

  .nav-list {
    max-width: 60vw;
  }

  .nav-item {
    font-size: 1.2em;
    padding: 1em;
  }

  .business-title {
    display:none;
  }
}

@media (max-width: 424px) {
  .top-nav {
    width: 100vh;
    -ms-flex-pack: start;
        justify-content: flex-start;
  }

  .nav-list {
    max-width: 75vw;
  }

  .nav-item {
    font-size: 1.2em;
    padding: 0.25em;
  }

  .business-title {
    display:none;
  }

  #photos {
  -webkit-column-count: 1;
  column-count:         1;
  }

  .price-container {
    -webkit-column-count: 1;
    column-count:         1;
    height: auto;
  }
}


